<template>
    <div>
        <select v-model="selected">
            <option
                    v-for="operator in operators"
                    :value="operator.key"
                    :key="operator.key">
                {{operator.name}}
            </option>
        </select>
    </div>
</template>

<script>


    export default {
        name: "currencySelector",
        props: ['value'],
        data() {
            return {
                operators: [
                    {key: "eur", name: "EUR"},
                    {key: "gbp", name: "GBP"},
                    {key: "usd", name: 'USD'},
                    {key: "rub", name: "RUB"}
                ],
            }
        },
        computed: {
            selected: {
                get() {
                    return this.value
                },
                set(val) {
                    this.$emit('input', val)
                }
            }
        }
    }


</script>
