<script>

    import tHistory from "../components/tables/t-history_a3"
    import incomingExternal from "../components/forms/incomingExternal"
    import {mapGetters, mapActions} from 'vuex'
    export default{
        name: "admin3",
        components: {
            tHistory,
            incomingExternal
        },
        data: function(){
            return{
                transactions: [],
                currentTab: "tHistory",
                tFilter: {
                    status: "approved",
                    type: "external"
                }
                
            }
        },
        methods:{
            async update(){
            await this.fetchTransactions().then(()=>{
                console.log('transactions ')
                this.transactions = this.allTransactions
               this.loading = false
            }).catch(error => {
                this.err = error
                console.log(error)
            })
            await this.fetchUsers().then(()=>{
                
                this.user = this.allUsers[0]
            }).catch(error => {
                
                this.err = error
                console.log(error)
            })
        },
            ...mapActions(['fetchUsers', 'fetchTransactions']),
            logOut(){
                localStorage.clear()
                this.$router.push('/login')
            }
        },
        computed: {
            ...mapGetters(['allUsers', 'allTransactions']),
            currentTabComponent (){
                console.log(this.currentTab)
                return this.currentTab
            },
        },
         async mounted(){
            await this.fetchTransactions().then(()=>{
                console.log('transactions ')
            this.transactions = this.allTransactions
               this.loading = false
            }).catch(error => {
                this.err = error
                console.log(error)
            })
            await this.fetchUsers().then(()=>{
                
                this.user = this.allUsers[0]
            }).catch(error => {
                
                this.err = error
                console.log(error)
            })
        }
    }
</script>
