<script>

    
    export default{
        name: "t-history",
        data: function(){
            return{
                
            }
        },
        props: {
            transactions: Array,
            userName: Object,
            users: Array,
            tFilter: Object
        },
        methods: {
            getCurrencySign(currency){
                const signs = {
                    "eur" : "€",
                    "gbp" : "£",
                    'usd' : "$",
                    "rub" : "₽"
                    }
                let currencySign = signs[currency] 
                return currencySign
          },
          async execute(id, status){
                console.log(id + " " + status)
                let update = {
                    id: id,
                    status: status
                }
                this.$emit('refresh')
                let response = await fetch("http://localhost:3000/updatetransaction", {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json;charset=utf-8'
                        },
                    body: JSON.stringify(update)
                    }).then( data => {
                        console.log(data)
                        
                    })
                }
        },
        computed: {
            filteredTransactions(){
                let filter = this.tFilter
                if (filter.status){
                    console.log(filter.status)
                    let transactions = this.transactions
                    transactions = transactions.filter( t => t.status == filter.status && t.type == filter.type)
                    return transactions
                }  
            }
        },
        filters: {
            upperCase(value){
                if (!value) return ''
                return value.toUpperCase()

            },
            dateFormat(value){
                if (!value) return ''
                value = value.slice(0, 10).replace(/-/g, ".")
                return value
            },
            fixed(value){
                if (!value) return ''
                value = value.toFixed(2)
                return value
            },
            formatAccount(value){
                if (!value) return ''
                value.toString()
                value = "0" + value
                let output = value.substr(0, 3)
                output += " " + value.substr(3, 2)
                output += " " + value.substr(5, 3)
                output += " " + value.substr(8, 4)
                return output
            },
            getCurrencySign(currency){
                const signs = {
                    "eur" : "€",
                    "gbp" : "£",
                    'usd' : "$",
                    "rub" : "₽"
                    }
                let currencySign = signs[currency] 
                return currencySign
            },
            // getStatusLabel(status){
            //   const labels = {
            //       "approved": require("@/assets/table_approved.png"),
            //       "rejected": require("@/assets/table_rejected.png"),
            //     //   "pending": "pending"
            //     }
            //   return labels[status] || "pending"
            //  }
        }
    }
</script>
