        filters:{
            formatAccount(account){
                account = "0" + account.toString()
            
               
                let output = account.substr(0, 3)
                output += " " + account.substr(3, 2)
                output += " " + account.substr(5, 3)
                output += " " + account.substr(8, 4)
               
                return output
            },
            formatTransactionID(value){ 
                
                let a = value.toString()
                let b = ""
                for(let i=0; i<19-a.length; i++){
                    b+="0"
                }
                let position = 0
                let output = [a.slice(0, position), b, a.slice(position)].join('')
                return output     
            },
            formatDate(value){

                if (!value) return ''
                value = value.toString()
                value = value.slice(3, 16).replace(/-/g, ".")
                return value
                
            }
        },
        methods: {
           checkValue(type, amount, available){
