                      this.transaction.transactionDetails.amount = this.user.balance.total[this.selected] - this.user.balance.blocked[this.selected]
                      this.transaction.transactionDetails.amount.toFixed(2)
                    }
                },
           
            async makePayment(){
                let newPayment = this.transaction
                newPayment.transactionDetails.currency = this.selectedCurrency
                newPayment.transactionDetails.bankAccount = this.getAccount
                
                
                let response = await fetch("http://localhost:3000/makepayment", {
                    method: 'POST',
                    headers: {
