                    available: this.user.balance.total[this.selected] - this.user.balance.blocked[this.selected].toFixed(2),
                    blocked: this.user.balance.blocked[this.selected].toFixed(2)
                    }
                return balance
            },
        
            selectedCurrency(){
                return this.selected
            },
            getAccount(){
                let selected = this.selected
                if(selected){
                     const accounts = {
                    "eur" : this.user.accounts.eur,
                    "gbp" : this.user.accounts.gbp,
                    'usd' : this.user.accounts.usd,
                    "rub" : this.user.accounts.rub
                    }
                let account = accounts[selected]
                this.$emit('recieveAccount', account)
                return account
                }
               
          },
