                    "gbp" : "£",
                    'usd' : "$",
                    "rub" : "₽"
                    }
                let currencySign = signs[this.selected]
                   
                return currencySign
          },
          getRate(){
              return this.currency[this.sellSelected][this.buySelected]
          }
